.html{
  margin:0;
  padding:0;
  overflow: hidden;
  font-family: 'Roboto', sans-serif;
}
.body{
   margin:0;
  padding:0;
  overflow: hidden;
}
.container {
  height: 100vh;
  background-image: url(/static/img/fondo2.d39c9f6.jpg);
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0;
  padding:0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-line-pack: distribute;
      align-content: space-around;
}
.derecha {
margin-left: auto;
margin-right:20px;
height: auto;
}
.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.wrap {
  margin-left: auto;
  margin-right: auto;
}
.puerta {
  height: 150px;
  width: 150px;
  background-image: url(data:image/png;base64,);
  background-size: contain;
  background-color: lightgoldenrodyellow;
  margin: 10px;
  border-radius: 6px;
  cursor: pointer;
}
.puerta::before {
  content: "";
  display: block;
  position: relative;
  top: -7px;
  left: -3px;
  right: 0;
  height: 23px;
  background: url(data:image/png;base64,)
      no-repeat 0 0,
    url(data:image/png;base64,)
      no-repeat 50% 0,
    url(data:image/png;base64,)
      no-repeat 100% 0;
}
.numbers {
  font-family: "Dancing Script", cursive;
  font-size: 50px;
  color: #8b0000;
  -webkit-text-stroke:.5px  silver;
  cursor: pointer;
}
.titulo-principal {
 font-family: 'Amatic SC', cursive;
  font-size: 50px;
  color: #ffffff;
  margin: 5px;
}
.footer{
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  justify-self: left;
  color:oldlace;
  margin: 5px;
  z-index: 1;
}

