}
.mensaje {
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  margin: 20px;
  line-height: 1.5;
  text-align: center;
  text-justify: center;
}
.foto {
  height: 400px;
  border-radius: 6px;
}
.video{
  width: 560px;
        height: 315px;
}

